body {
  font-family: "Source Sans Pro",sans-serif; }

.clearfix::after {
  content: "";
  display: table;
  clear: both; }

.countainer {
  margin: 0 auto;
  width: 1200px; }

.log {
  position: relative;
  float: left;
  padding: 2px 0; }

.nav {
  float: right;
  line-height: 100px; }
  .nav li {
    float: left; }
    .nav li a {
      padding: 0 12px;
      color: #333333;
      text-decoration: none;
      font-size: 13px; }

.bundery::before {
  content: "|";
  padding: 0 12px; }

.nav .active a {
  color: #d6b1b1; }

.slider {
  width: 100%;
  height: auto; }

.col-4, .col-6 {
  float: left;
  padding: 0 10px; }

.col-4 {
  width: 33.333%; }

.col-6 {
  width: 50%; }

.categories {
  padding-top: 100px; }

.row {
  margin: 0 -10px; }

.category {
  position: relative;
  margin-bottom: 20px; }
  .category img {
    width: 100%;
    height: auto; }
  .category .title {
    position: absolute;
    color: #222222;
    text-align: right;
    font-size: 24px;
    top: 35%;
    right: 10%;
    font-weight: 300; }

.category .light {
  color: #fff; }

.recent_product {
  padding: 100px 0; }

.intro {
  margin-bottom: 100px;
  text-align: center; }
  .intro h2 {
    margin: 0 auto;
    padding-bottom: 10px;
    text-transform: uppercase;
    font-weight: normal;
    letter-spacing: 2px; }
  .intro hr {
    width: 5%;
    border-width: 1.5px;
    border-color: #595959; }

.intro p {
  padding-top: 10px;
  color: gray; }

.products li {
  float: left; }

.products img {
  width: 100%;
  height: auto; }

ul {
  margin: 0;
  padding-left: 0; }

.product .info {
  padding: 15px 0;
  text-align: center; }
  .product .info .title {
    margin: 0; }

.product .title a {
  color: #222222;
  text-decoration: none;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: normal;
  letter-spacing: 1px; }

.product .price {
  color: #7e7e7e;
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 30px; }

.thumbnail {
  position: relative; }
  .thumbnail .cover {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    border: 20px solid rgba(255, 255, 255, 0.6);
    background-color: rgba(255, 255, 255, 0.9);
    text-align: center;
    opacity: 0;
    transition: opacity 0.3s; }
  .thumbnail a {
    position: absolute;
    color: #333333;
    text-decoration: none;
    top: 45%; }

.thumbnail .cover:hover {
  opacity: 1; }

.event {
  padding: 150px 0;
  background-image: url("http://lorempixel.com/1920/1080/");
  background-size: cover;
  background-position: 50% 50%;
  background-attachment: fixed; }
  .event a {
    text-decoration: none; }

.event .title {
  margin: 0;
  color: #fff;
  font-weight: 100;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 1.5;
  font-size: 70px; }

.event .subtitle {
  color: #fff;
  font-weight: 100;
  letter-spacing: 2px;
  text-transform: uppercase;
  line-height: 1.5;
  font-size: 24px; }

.button {
  display: inline-block;
  margin-top: 30px;
  padding: 15px 30px;
  background: #fff;
  color: #222222;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px; }

p {
  color: #7e7e7e;
  line-height: 30px; }

.feature .col-6 {
  padding: 0 30px; }

.feature img {
  width: 100%;
  height: auto; }

.feature {
  padding-bottom: 100px; }
  .feature .info {
    margin-top: 70px;
    text-align: center; }
    .feature .info .title, .feature .info .name {
      text-transform: uppercase;
      margin-bottom: 30px; }
    .feature .info .price {
      font-size: 26px;
      font-weight: 100; }

.feature .title, .feature .price {
  color: #d6b1b1; }

.feature p {
  margin: 0;
  margin-bottom: 30px;
  padding-top: 30px; }

.button.border {
  border: 1px solid #222222; }

.outlets {
  padding: 100px 0; }
  .outlets h3 {
    margin: 0;
    margin-bottom: 30px;
    text-transform: uppercase;
    font-weight: normal; }

.outlets .info {
  color: #7e7e7e;
  line-height: 30px; }
  .outlets .info a {
    color: #7e7e7e;
    text-decoration: none; }

.outlets .map a {
  color: #d6b1b1;
  text-transform: uppercase;
  text-decoration: none; }
